@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600&display=swap);
.movies .moviecard{
    position: relative;
}
.movies .moviecard .movie{
   /* background-color: white;*/
    margin-bottom: 70px;
    font-weight: 300;
    padding: 10px;
    color:#adaeb9;
    transition : 350ms linear;
    text-align : left;
    z-index: 1;
}

 .movies .moviecard .movie__data{
    max-height: 340px;
    top:30%;left:50%;transform: translate(-50%, -50%);
    position: absolute;
    font-weight: 300;
    padding: 10px;
    max-width:250px;
    min-width: 230px;
    overflow: auto;
}
.movies .moviecard .movie__data::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}
.movies .moviecard .movie .movie__head{
    display: grid;
    max-width: 230px;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: 180px 50px;
    align-content: stretch;
}
.movies .moviecard:hover .movie{
    margin:0 20px;
    transform:scale(1.2);
    
}
.movies .moviecard:hover img{
    opacity: 0.3;
    
}
.movies .moviecard:hover .movie__data{
    transition : 550ms all;
    -webkit-animation: fadein 2s;
            animation: fadein 2s;
}


.movie img{
    display:table;
    margin-left: auto;
    margin-right: auto;
    position :relative;
    height: auto; width: auto;
}


.movie .movie__title {
    display:table;
    margin-left: auto;
    margin-right: auto;
    max-width: 230px;
    font-size : 25px;
    color: #dfdfdf;
    font-weight :400;
}
.movie .movie__rating{
    display:table;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50%;
    font-size : 25px;
    color: #FF364E;
    font-weight :400;
}

.movie__data .movie__genres{
    list-style : none;
    padding : 0;
    display : flex;
    flex-wrap : wrap;
    margin : 5px 0px;
}

.movie__genres li, .movie__data .movie__year{
    margin-right :10px;
    font-size : 14px;
}

@-webkit-keyframes fadein{
    from{
        opacity:0;
    }
    to{
        opacity:1;
    }
}

@keyframes fadein{
    from{
        opacity:0;
    }
    to{
        opacity:1;
    }
}
*{   /*universal selector*/
  box-sizing:border-box /*border두께의 크기포함해서 sizing*/
}
.container{
  width : 100%;
}
header{
  width:100%;
  margin : 20px;
  margin-bottom: 300px;
}

/*mobile*/
@media screen and (max-width : 767px) {
  header{
    margin-bottom: 150px;
  }
  
}

@media screen and (max-width : 767px) {
  header img{
    width: auto; height: auto;
    max-width: 180px;
    max-height: 30px;
  }
}

body{
  margin : 0;
  padding : 0;
  font-family: 'Raleway';
  background-color: #1F2020;
  height : 100%;
  width : 100%;
  color : #ffffff;
}



.movies{
  margin-left: 10px;
  margin-right:10px;
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  /*grid-template-rows: repeat(7,minmax(500px,auto));*/
  /*grid-template-columns: repeat(6,1fr);*/
  scroll-behavior: smooth;
  grid-gap : 20px;
}
.loader{
  width :100%;
}

.loader img{
  width : 50px;
  height: 50px;
  position:absolute;
  left:50%;
  top:50%;
  -webkit-animation-name: spinImg;
          animation-name: spinImg;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}
@media screen and (max-width : 767px) {
  .loader img{
    width: 40px;
    height: 40px;
  }
}

@-webkit-keyframes spinImg{
  from{
    transform: translate(-50%,-50%)rotate(0);
  }
  to{
    transform:translate(-50%,-50%) rotate(360deg);
  }
}

@keyframes spinImg{
  from{
    transform: translate(-50%,-50%)rotate(0);
  }
  to{
    transform:translate(-50%,-50%) rotate(360deg);
  }
}
